('/*\nCopyright DEPP © 2017 - Ministère de l\'éducation nationale  \n*/\n\n\n/*add some style, need to scope (using the typeIdentifier as class prefix) every style declaration to prevent collision */\n\n.relatemInteraction {\n    font-size: unset;\n    text-align: left;\n    background: #fff;\n    font-weight: 100;\n    color: black;\n    padding-bottom: 70px!important;\n}\n\n.qti-customInteraction .relatem .ansrdm {\n    display: none;\n}\n\n.qti-customInteraction .relatem {\n    width: 100%;\n    padding: 10px 20px 16px;\n}\n\n.qti-customInteraction .relatem .container {\n    overflow: hidden;\n    width: 960px;\n}\n\n.qti-customInteraction .relatem .activVF:hover {\n    background-color: orange;\n    cursor: pointer;\n    opacity: 0.8;\n}\n\n.qti-customInteraction .relatem .activVF {\n    height: 95px;\n    background-image: url(\'data:image/png;base64,\');\n    background-repeat: no-repeat;\n    width: 123px;\n}\n\n.qti-customInteraction .relatem .activG:hover {\n    background-color: orange;\n    cursor: pointer;\n    opacity: 0.8;\n}\n\n.qti-customInteraction .relatem .activG {\n    background-image: url(\'data:image/png;base64,\');\n    height: 95px;\n    background-repeat: no-repeat;\n    width: 123px;\n}\n\n.qti-customInteraction .relatem .activGlobal:hover {\n    background-color: orange;\n    cursor: pointer;\n    opacity: 0.8;\n}\n\n.qti-customInteraction .relatem .activGlobal {\n    background-image: url(\'data:image/png;base64,\');\n    height: 95px;\n    background-repeat: no-repeat;\n    width: 150px;\n}\n\n.qti-customInteraction .relatem .cactus {\n    height: 366px;\n    position: absolute;\n    left: 10px;\n    z-index: 1000;\n}\n\n.qti-customInteraction .relatem .commande {\n    position: absolute;\n    margin-top: 400px;\n    text-align: center;\n    width: 700px;\n}\n\n.qti-customInteraction .relatem .commande img {\n    height: 85px;\n}\n\n.qti-customInteraction .relatem .imgline {\n    height: 30px;\n    width: 150px;\n}\n\n.qti-customInteraction .relatem .commande table {\n    width: 450px;\n    margin-left: auto;\n    margin-right: auto;\n}\n\n.qti-customInteraction .relatem .vuegars {\n    background-image: url(\'data:image/png;base64,\');\n    background-repeat: no-repeat;\n    width: 650px!important;\n    height: 600px;\n    display: none;\n    overflow: hidden;\n    position: absolute;\n}\n\n.qti-customInteraction .relatem .vueglobale {\n    width: 650px!important;\n    height: 366px;\n    background-image: url(\'data:image/png;base64,\');\n    position: absolute;\n}\n\n.qti-customInteraction .relatem .vueglobale img {\n    width: 700px;\n}\n\n.qti-customInteraction .relatem .vfimg {\n    position: absolute;\n    height: 366px!important;\n    max-width: 1512px!important;\n    margin-left: -1250px;\n    -webkit-animation: relatem-goguy 3s linear;\n    -webkit-animation-fill-mode: forwards;\n    -moz-animation: relatem-goguy 3s linear;\n    -moz-animation-fill-mode: forwards;\n    animation: relatem-goguy 3s linear;\n    animation-fill-mode: forwards;\n}\n\n.qti-customInteraction .relatem .vuefille {\n    position: absolute;\n    display: none;\n    width: 700px;\n    height: 530px;\n    overflow: hidden;\n}\n\n.qti-customInteraction .relatem .vue {\n    border: none!important;\n    position: absolute;\n    opacity: 0;\n}\n\n.qti-customInteraction .relatem .vue:hover {\n    opacity: 0.4;\n}\n\n.qti-customInteraction .relatem td {\n    border: 2px grey solid;\n}\n\n.qti-customInteraction .relatem.legende {\n    font-family: arial;\n    font-size: 0.8em;\n}\n\n.qti-customInteraction .relatem rect {\n    /* fill:red!important; */\n    animation-name: colored;\n    animation-duration: 3s;\n    animation-iteration-count: 6;\n    -webkit-animation-name: colored;\n    -webkit-animation-duration: 3s;\n    -webkit-animation-iteration-count: 6;\n    -moz-animation-name: colored;\n    -moz-animation-duration: 3s;\n    -moz-animation-iteration-count: 6;\n}\n\n\n/* Translation de la moto */\n\n@keyframes relatem-gotmoto {\n    from {\n        transform: translate(-500px);\n        -webkit-transform: translate(-500px);\n        -moz-transform: translate(-500px);\n    }\n    to {\n        transform: translate(920px);\n        -webkit-transform: translate(920px);\n        -moz-transform: translate(920px);\n    }\n}\n\n@keyframes relatem-goguy {\n    from {\n        transform: translate(420px);\n        -webkit-transform: translate(420px);\n        -moz-transform: translate(420px);\n    }\n    to {\n        transform: translate(1250px);\n        -webkit-transform: translate(1250px);\n        -moz-transform: translate(1250px);\n    }\n}\n\n\n/* Rotation des roues */\n\n@keyframes relatem-rotating {\n    from {\n        transform: rotate(0deg);\n        -webkit-transform: rotate(0deg);\n        -moz-transform: rotate(0deg);\n    }\n    to {\n        transform: rotate(360deg);\n        -webkit-transform: rotate(360deg);\n        -moz-transform: rotate(360deg);\n    }\n}\n\n.qti-customInteraction .relatem .roue {\n    animation: relatem-rotating 0.5s linear infinite;\n    -webkit-animation: relatem-rotating 0.5s linear infinite;\n    -moz-animation: relatem-rotating 0.5s linear infinite;\n    transform-origin: 50%;\n}\n\n.qti-customInteraction .relatem .roue2 {\n    animation: relatem-rotating 0.5s linear infinite;\n    -webkit-animation: relatem-rotating 0.5s linear infinite;\n    -moz-animation: relatem-rotating 0.5s linear infinite;\n    transform-origin: 50%;\n}\n\n.qti-customInteraction .relatem .moto {\n    top: -30px;\n    width: 300px;\n    height: 300px;\n}\n\n.qti-customInteraction .relatem .roue {\n    top: 256px;\n    left: 263px!important;\n    width: 110px;\n    height: 110px;\n}\n\n.qti-customInteraction .relatem .roue2 {\n    width: 110px;\n    height: 110px;\n}\n\n.qti-customInteraction .relatem .moto {\n    background-repeat: no-repeat;\n    width: 700px;\n    top: -26px;\n    position: absolute;\n    animation: relatem-gotmoto 3s linear;\n    animation-fill-mode: forwards;\n}\n\n.qti-customInteraction .relatem .roue {\n    position: absolute;\n    top: 274px;\n    left: 327px;\n    z-index: 0;\n}\n\n.qti-customInteraction .relatem .roue2 {\n    position: absolute;\n    top: 274px;\n    left: 35px;\n    z-index: 0;\n}\n\n.qti-customInteraction .relatem .moto {\n    position: absolute;\n    z-index: 2;\n}\n\n.qti-customInteraction .relatem {\n    width: 960px;\n    height: 520px;\n}');
